import axios from "axios";
import React from "react";
import youtube, { tra } from "./api/youtube";
import Searchbar from "./components/Searchbar";
import VideoDetail from "./components/VideoDetail";
import VideoList from "./components/VideoList";

function App() {
  const [videos, setVideos] = React.useState([])
  const [selectedVideo, setSelectedVideo] = React.useState(null)

  const handleSubmit = async (termFromSearchBar) => {
    const response = await youtube.get('/search', {
      params: {
        q: termFromSearchBar
      }
    })

    setVideos(response.data.items)
    console.log("this is resp", response);
  }

  const handleVideoSelect = (video) => {
    setSelectedVideo(video)
  }

  const onClickBut = async () => {
    let insult = await axios.get(`https://cors-anywhere.herokuapp.com/https://evilinsult.com/generate_insult.php?lang=ru&type=json`)
    console.log(insult)
  }

  return (
    <div className='ui container' style={{ marginTop: '1em' }}>
      <button onClick= {onClickBut}>fdgfdgfdg</button>
      <Searchbar handleFormSubmit={handleSubmit} />
      <div className='ui grid'>
        <div className="ui row">
          <div className="eleven wide column">
            <VideoDetail video={selectedVideo} />
          </div>
          <div className="five wide column">
            <VideoList handleVideoSelect={handleVideoSelect} videos={videos} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
